import { products } from "@/app/Lib/data";
import BagClientComponent from "./BagClientComponent";

export default function BagDetailPage({ params }) {
  const bag = products.find((item) => item.slug === params.slug);

  if (!bag) {
    return <main className="p-10 text-xl">Bag not found</main>;
  }

  return <BagClientComponent bag={bag} />;
}